import React, { useState } from 'react';
import { Row, Col, Image, Button } from 'react-bootstrap';
import { Route, Switch, Redirect, useRouteMatch } from 'react-router';
import { Optionnal } from '../../../components/Optionnal';
import { logEvent } from '../../../components/TawkTo';
import { ConfigureRack, getNewConfiguration as rGetNewConfiguration, validConfiguration, RackConfiguration, getTotalPrice, getPartNumber } from './ConfigureRack';
import { ConfigureShippingPayment, getNewConfiguration as spGetNewConfiguration, ShippingPaymentConfiguration } from './ConfigureShippingPayment';
import './Racks.scss';

function Racks() {
  let { path, url } = useRouteMatch();

  const [products, setProducts] = useState<RackConfiguration[]>([rGetNewConfiguration()]);
  const [shippingPayment, setShippingPayment] = useState<ShippingPaymentConfiguration>(spGetNewConfiguration());

  function onProductChange(index: number, product: RackConfiguration) {
    const newProducts = [...products];
    newProducts[index] = product;
    setProducts(newProducts);

    logEvent('productChange', {
      nbProducts: newProducts.length,
      basket: newProducts.map(getPartNumber).join(', '),
      totalProduct: getProductsPrice(newProducts)
    });
  }

  function onAddProduct() {
    setProducts([...products, rGetNewConfiguration()]);
  }

  function getProductsPrice(products: RackConfiguration[]): number {
    return products.reduce((p, product) => p + (validConfiguration(product) ? getTotalPrice(product) : 0), 0);
  }

  function onShippingChange(conf: ShippingPaymentConfiguration, totalPrice: number) {
    setShippingPayment(conf);

    logEvent('shippingChange', {
      products: products.map(getPartNumber).join(', '),
      shipping: conf.shipping[0],
      payment: conf.payment[0],
      totalPrice: totalPrice + '€'
    });
  }

  return (
    <div className="orderRacks">
      <Row className="mt-4">
        <Col md={5}>
          <Image src="../img/racks/racks.jpg" thumbnail alt="Support pour filières de Philips Pasta Maker" />
        </Col>
        <Col md={7}>
          <h2>Support pour filières</h2>
          <p>Disponible pour les machines <strong>Philips Pasta Maker Avance</strong> et <strong>Philips Pasta Maker Viva</strong>, en plusieurs capacités et en plusieurs couleurs.</p>
          <p>Faites vos choix pour configurer votre support puis passez commande ici-même.</p>
        </Col>
      </Row>
      <Row className="mt-3">
        <Col>
          {products.map((product, index) =>
            <ConfigureRack onChange={conf => onProductChange(index, conf)} onAdd={products.length - 1 === index ? onAddProduct : undefined} key={'product' + index} />
          )}
        </Col>
      </Row>
      <Optionnal displayed={products.some(prod => validConfiguration(prod))}>
        <Row className="mt-3 mb-4">
          <Col>
            <ConfigureShippingPayment basketTotal={getProductsPrice(products)} onChange={onShippingChange} />
          </Col>
        </Row>
      </Optionnal>
    </div>
  )
}

export default Racks;
