import React, { useState } from "react";
import { StepPayment } from "./steps/StepPayment";
import { StepPrice } from "./steps/StepPrice";
import { StepShipping } from "./steps/StepShipping";

export interface ShippingPaymentConfiguration {
  shipping: [string, number],
  payment: [string, number]
}

export function getNewConfiguration(): ShippingPaymentConfiguration {
  return {
    shipping: ['', 0],
    payment: ['', 0]
  };
}

export function ConfigureShippingPayment(props: {
  basketTotal: number,
  onChange: (conf: ShippingPaymentConfiguration, price: number) => void
}) {
  const [stepIndex, setStepIndex] = useState(0);
  const [config, setConfig] = useState<ShippingPaymentConfiguration>(getNewConfiguration());

  function updateConfig(attribute: keyof ShippingPaymentConfiguration, value: [string, number]) {
    const newConfig = { ...config, [attribute]: value };
    setConfig(newConfig);
    props.onChange(newConfig, getTotalPrice());
  }

  function getTotalPrice(): number {
    const fixAmount = props.basketTotal + config.shipping[1];
    const paymentMultiplier = 1 + (config.payment[1] / 100);
    return Math.round(fixAmount * paymentMultiplier * 10) / 10;
  }

  return (
    <div className="configureShippingPayment clearfix">
      <div className="myAccordion w-100 border border-info">
        <StepShipping expanded={stepIndex === 0} onSelected={() => setStepIndex(0)} onNext={() => setStepIndex(1)} onChange={val => updateConfig('shipping', val)} />
        <StepPayment displayed={stepIndex >= 1} expanded={stepIndex === 1} onSelected={() => setStepIndex(1)} onNext={() => setStepIndex(2)} onChange={val => updateConfig('payment', val)} />
        <StepPrice displayed={stepIndex === 2} total={getTotalPrice()} />
      </div>
    </div>
  );
}