import React, { useEffect } from 'react';

declare global {
  var Tawk_API: any;
  var Tawk_LoadStart: Date
}

export function logEvent(eventName: string, data?: { [key: string]: string | number}, cbError?: (e: string) => void): void {
  cbError = cbError ? cbError : (err) => {
    console.error('Fail to log event: ', eventName);
  }

  if (data !== undefined) {
    Tawk_API.addEvent(eventName, data, cbError);
  }
  else {
    Tawk_API.addEvent(eventName, cbError);
  }
}

export function TawkTo(props: {
  propertyId: string,
  apiKey: string
}) {
  useEffect(() => {
    window.Tawk_API = window.Tawk_API || {};
    window.Tawk_LoadStart = new Date();

    var s1 = document.createElement("script"), s0 = document.getElementsByTagName("script")[0];
    s1.async = true;
    s1.src = 'https://embed.tawk.to/' + props.propertyId + '/' + props.apiKey;
    s1.charset = 'UTF-8';
    s1.setAttribute('crossorigin', '*');
    if (s0.parentNode)
      s0.parentNode.insertBefore(s1, s0);
  });

  return <div className="tawkTo" />;
}